import {vec2, vec3, vec4, mat4} from 'gl-matrix';
import Drawable from './Drawable';
import {gl} from '../../globals';

var activeProgram: WebGLProgram = null;

export class Shader {
  shader: WebGLShader;

  constructor(type: number, source: string) {
    this.shader = gl.createShader(type);
    gl.shaderSource(this.shader, source);
    gl.compileShader(this.shader);

    if (!gl.getShaderParameter(this.shader, gl.COMPILE_STATUS)) {
      throw gl.getShaderInfoLog(this.shader);
    }
  }
};

class ShaderProgram {
  prog: WebGLProgram;

  attrPos: number;
  attrNor: number;
  attrCol: number;
  attrTan: number;

  unifModel: WebGLUniformLocation;
  unifModelInvTr: WebGLUniformLocation;
  unifViewProj: WebGLUniformLocation;
  unifColor: WebGLUniformLocation;

  constructor(shaders: Array<Shader>) {
    this.prog = gl.createProgram();

    for (let shader of shaders) {
      gl.attachShader(this.prog, shader.shader);
    }
    gl.linkProgram(this.prog);
    if (!gl.getProgramParameter(this.prog, gl.LINK_STATUS)) {
      throw gl.getProgramInfoLog(this.prog);
    }

    this.attrPos = gl.getAttribLocation(this.prog, "vs_Pos");
    this.attrNor = gl.getAttribLocation(this.prog, "vs_Nor");
    this.attrTan = gl.getAttribLocation(this.prog, "vs_Tan");
    this.attrCol = gl.getAttribLocation(this.prog, "vs_Col");
    this.unifModel      = gl.getUniformLocation(this.prog, "u_Model");
    this.unifModelInvTr = gl.getUniformLocation(this.prog, "u_ModelInvTr");
    this.unifViewProj   = gl.getUniformLocation(this.prog, "u_ViewProj");
    this.unifColor      = gl.getUniformLocation(this.prog, "u_Color");
  }

  use() {
    if (activeProgram !== this.prog) {
      gl.useProgram(this.prog);
      activeProgram = this.prog;
    }
  }

  setModelMatrix(model: mat4) {
    this.use();
    if (this.unifModel !== -1) {
      gl.uniformMatrix4fv(this.unifModel, false, model);
    }

    if (this.unifModelInvTr !== -1) {
      let modelinvtr: mat4 = mat4.create();
      mat4.transpose(modelinvtr, model);
      mat4.invert(modelinvtr, modelinvtr);
      gl.uniformMatrix4fv(this.unifModelInvTr, false, modelinvtr);
    }
  }

  setViewProjMatrix(vp: mat4) {
    this.use();
    if (this.unifViewProj !== -1) {
      gl.uniformMatrix4fv(this.unifViewProj, false, vp);
    }
  }

  setUniformMat4(name: string, mat: mat4) {
    this.use();
    gl.uniformMatrix4fv(gl.getUniformLocation(this.prog, name), false, mat);
  }

  setGeometryColor(color: vec4) {
    this.use();
    if (this.unifColor !== -1) {
      gl.uniform4fv(this.unifColor, color);
    }
  }

  setUniformFloat(name: string, value: number) {
    this.use();
    gl.uniform1f(gl.getUniformLocation(this.prog, name), value);
  }

  setUniformBool(name: string, value: boolean) {
    this.use();
    gl.uniform1i(gl.getUniformLocation(this.prog, name), value ? 1 : 0);
  }

  setUniformVec2(name: string, value: vec2) {
    this.use();
    gl.uniform2fv(gl.getUniformLocation(this.prog, name), value);
  }

  setUniformVec3(name: string, value: vec3) {
    this.use();
    gl.uniform3fv(gl.getUniformLocation(this.prog, name), value);
  }

  setEyeRefUp(eye: any, center: any, up: any) {
    this.use();
    let eye_unif = gl.getUniformLocation(this.prog, "u_Eye");
    let ref_unif = gl.getUniformLocation(this.prog, "u_Ref");
    let up_unif = gl.getUniformLocation(this.prog, "u_Up");
    if (eye_unif !== -1) {
      gl.uniform3fv(eye_unif, eye);
    }
    if (ref_unif !== -1) {
      gl.uniform3fv(ref_unif, center);
    }
    if (up_unif !== -1) {
      gl.uniform3fv(up_unif, up);
    }
  }

  draw(d: Drawable) {
    this.use();

    if (this.attrPos != -1 && d.bindPos()) {
      gl.enableVertexAttribArray(this.attrPos);
      gl.vertexAttribPointer(this.attrPos, 3, gl.FLOAT, false, 0, 0);
    }

    if (this.attrNor != -1 && d.bindNor()) {
      gl.enableVertexAttribArray(this.attrNor);
      gl.vertexAttribPointer(this.attrNor, 3, gl.FLOAT, false, 0, 0);
    }

    if (this.attrTan != -1 && d.bindTan()) {
      gl.enableVertexAttribArray(this.attrTan);
      gl.vertexAttribPointer(this.attrTan, 3, gl.FLOAT, false, 0, 0);
    }

    d.bindIdx();
    gl.drawElements(d.drawMode(), d.elemCount(), gl.UNSIGNED_INT, 0);

    if (this.attrPos != -1) gl.disableVertexAttribArray(this.attrPos);
    if (this.attrNor != -1) gl.disableVertexAttribArray(this.attrNor);
    if (this.attrTan != -1) gl.disableVertexAttribArray(this.attrTan);
  }
};

export default ShaderProgram;
